<script>
const urlApi = 'https://api.airtable.com/v0/appmJgMzCXboSeEh3/Conferencistas?&view=Grid%20view';
const autorizacion = 'Bearer keyiXBafVcXh6RM5t';

export default {
  name: 'App',
  mounted: function() {
    this.getOradores();
  },
  methods: {
    getOradores: function() {
      fetch(urlApi, {
        headers: {
            'Authorization': autorizacion
        }
    })
        .then((response) => response.json())
        .then((json) => this.$store.state.oradores = json.records);
    }
  },
}
</script>