<template>
  <router-link :to="{ name: 'About', params: { oradorId: id }}">
    <div class="profile">
      <img class="profile_img" :src="avatar" :alt="nombre">
      <h3 class="profile_theme">{{ tema }} <span class="profile_date">{{ formatearFecha(fecha) }}</span></h3>
      <h4 class="profile_name">{{ nombre }}</h4>
    </div>
  </router-link>
</template>

<script>
import { format } from 'date-fns'

export default {
  name: 'Orador',
  props: {
    id: String
  },
  computed: {
    newOrador: function() {
      return this.$store.state.oradores.filter((orador) => {
        return orador.id === this.id
      })[0].fields
    },
    nombre: function() {
      return this.newOrador.name
    },
    avatar: function() {
      return this.newOrador.avatar[0].url
    },
    tema: function() {
      return this.newOrador.theme
    },
    fecha: function() {
      return this.newOrador.date
    },
},
  methods: {
    formatearFecha: function(fecha) {
      return format(new Date(fecha), 'dd/MM/yyyy')
    }
  }

}
</script>

