<template>
  <section class="formulario">
    <h2 class="formulario_title">Quieres asistir? Inscribete</h2>
    <form class="formulario_card">
      <label class="formulario_label">
        Nombre:
          <input class="formulario_input" type="text" placeholder="escribe tu nombre" v-model="newName">
        </label>
        <label class="formulario_label">
        Email:
          <input class="formulario_input" type="email" placeholder="escribe tu email" v-model="newEmail">
        </label>
        <button @click.prevent="addRecord" class="formulario_btn-submit" type="submit">enviar</button>
      </form>
      <div class="registro" v-if="exito">Felicidades Te has Registrado Correctamente</div>
  </section>
</template>

<script>
const apiCreate = 'https://api.airtable.com/v0/appmJgMzCXboSeEh3/Asistentes';
const autorizacion = 'Bearer keyiXBafVcXh6RM5t';

export default {
  name: 'Formulario',
  data: function() {
    return {
      exito: false,
      newName: '',
      newEmail: '',
    }
  },
  methods: {
    addRecord:function() {
        fetch(apiCreate, {
            headers: {
                'Content-type': 'application/json',
                'Authorization': autorizacion
            },
            method: 'POST',
            body: JSON.stringify({
                "records": [
                    {
                        "fields": {
                            "name": this.newName,
                            "email": this.newEmail,
                            // "date": (new Date()).toString()
                        }
                    }
                ]
            })
        })
        .then(() => {
          this.newName = ''
          this.newEmail = ''
          this.exito = true
        })
    }
  }
}
</script>

