<template>
  <div class="about">
    <div class="profile">
      <img class="profile_img" :src="miFicha.fields.avatar[0].url" :alt="miFicha.fields.name">
      <div class="profile_info">
        <h3 class="profile_theme">{{ miFicha.fields.theme }} <span class="profile_date">{{ formatearFecha(miFicha.fields.date) }}</span></h3>
        <h4 class="profile_name">{{ miFicha.fields.name }}</h4>
        <p class="profile_description">{{ miFicha.fields.descriptions }}</p>
      </div>
    </div>
    <button class="btn" @click="$router.push('/')">back</button>
  </div>
</template>

<script>
import { format } from 'date-fns'

export default {
  name: 'About',
  components: {
  },
  computed: {
    miFicha: function () {
      return this.$store.state.oradores.filter((actualFicha) => {
        return actualFicha.id === this.$route.params.oradorId
      })[0]
    }
  },
  methods: {
    formatearFecha: function(fecha) {
      return format(new Date(fecha), 'dd/MM/yyyy')
    }
  }
}
</script>

